<style lang="scss"></style>

<template>
  <div
    class="page-preview"
    :style="{ backgroundColor: pageBackgroundColor }"
  >
    <component
      :is="wgt.type"
      v-for="(wgt, index) in widgets"
      :key="`wgt-${index}`"
      mode="preview"
      :value="wgt"
    />
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
// import mixins from './../../plugin/mixins'
// const qs = require('qs')
export default {
  // mixins: [mixins],
  // components: { mNav },
  data () {
    return {
      pages: [],
      uuid: null,
      title: '',
      widgets: [],
      pageBackgroundColor: '#fff'
    }
  },
  created () {
    // this.getShopDesign()
  },
  mounted () {
    // 根元素比例转化
    // let docEl = document.documentElement
    // let clientWidth = docEl.clientWidth
    // if (!clientWidth) return
    // if (clientWidth >= 750) {
    //   docEl.style.fontSize = '100px'
    // } else {
    //   docEl.style.fontSize = 100 * (clientWidth / 750) + 'px'
    // }
    this.getShopDesign()
  },
  computed: {
    ...mapState({
      pagedata: (state) => state.pagedata
    })
  },
  methods: {
    ...mapMutations(['SET_PAGEDATA']),
    async getShopDesign () {
      const { widgets } = this.pagedata
      this.widgets = widgets
      const wpage = widgets.find(item => item.type == 'W0000')
      if (wpage) {
        this.pageBackgroundColor = wpage.backgroundColor
      }
    }
  }
}
</script>
